export default ".open-button {\n  background-color: #7fad39;\n  color: white;\n  padding: 16px 20px;\n  border: none;\n  cursor: pointer;\n  opacity: 0.8;\n  position: fixed;\n  bottom: 23px;\n  right: 28px;\n  border-radius: 50%;\n}\n\n/* The popup chat - hidden by default */\n\n.chat-popup {\n  display: none;\n  position: fixed;\n  bottom: 0;\n  right: 15px;\n  border-top-left-radius: 8px;\n  border-top-right-radius: 8px;\n  box-shadow: 2px 2px 2px 2px #7fad39;\n  z-index: 9;\n}\n\n/* Add styles to the form container */\n\n.form-container {\n  max-width: 400px;\n  min-width: 300px;\n  padding: 10px;\n  border: #FFFFFF;\n  border-top-left-radius: 8px;\n  border-top-right-radius: 8px;\n  background-color: white;\n}\n\n/* When the textarea gets focus, do something */\n\n.form-container input:focus {\n  background-color: #ddd;\n  outline: none;\n}\n\n/* Add a red background color to the cancel button */\n\n.form-container .cancel {\n  background-color: red;\n}\n\n/* Add some hover effects to buttons */\n\n.form-container .btn:hover, .open-button:hover {\n  opacity: 1;\n}\n\nul {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n}\n\n.chat-history ul li {\n  display: inline-block;\n  clear: both;\n  padding: 5px;\n  border-radius: 30px;\n  margin-bottom: 5px;\n  font-family: Helvetica, Arial, sans-serif;\n}\n\n.you {\n  background: #eee;\n  float: left;\n}\n\n.me {\n  float: right;\n  background: #7fad39;\n  color: #fff;\n}\n\n.you + .me {\n  border-bottom-right-radius: 5px;\n}\n\n.me + .me {\n  border-top-right-radius: 5px;\n  border-bottom-right-radius: 5px;\n}\n\n.me:last-of-type {\n  border-bottom-right-radius: 30px;\n}\n\n.chat-header {\n  border-bottom: 2px solid #f0f0f0;\n}\n\n.chat-footer {\n  border-top: 2px solid #f0f0f0;\n}\n\n.chat-history {\n  margin-top: 3px;\n  margin-bottom: 3px;\n}\n\n#history {\n  width: 100%;\n}\n\n#chat-message {\n  max-height: 300px;\n  min-height: 250px;\n  overflow-y: scroll;\n}\n/*# sourceMappingURL=data:application/json;base64